import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import {FormsModule} from '@angular/forms';

import { AppComponent } from './app.component';
import { PeopleComponent } from './people/people.component';
import {Routes,RouterModule} from '@angular/router';
import { HomeComponent } from './home/home.component';
import { WorkComponent } from './work/work.component';
import { ContactComponent } from './contact/contact.component';
import { StudentsComponent } from './students/students.component';
import { FacultyComponent } from './faculty/faculty.component';
import { ResearchComponent } from './research/research.component';

import { PostsComponent } from './home/posts/posts.component';
import { PostComponent } from './home/posts/post/post.component';
import { BackDirective } from './back.directive';
import { NewPostComponent } from './home/posts/new-post/new-post.component';
import { PostService } from './shared/post.service';

import {AppRoutingModule} from './app-routing.module';
import { CanDeactivateGuard } from './home/posts/new-post/can-deactivate-guard.service';
import {AngularFireModule} from 'angularfire2';
import {AngularFireDatabaseModule} from 'angularfire2/database';
import { TeacherComponent } from './faculty/teacher/teacher.component';
import { TeacherListComponent } from './faculty/teacher-list/teacher-list.component';
import { TeacherListEditComponent } from './faculty/teacher-list-edit/teacher-list-edit.component';

import{environment} from '../environments/environment';
import { TeacherService } from './faculty/shared/teacher.service';

 
@NgModule({
  declarations: [
    AppComponent,
    PeopleComponent,HomeComponent, WorkComponent, ContactComponent, StudentsComponent, FacultyComponent, ResearchComponent,  PostsComponent, PostComponent, BackDirective, NewPostComponent, TeacherComponent, TeacherListComponent, TeacherListEditComponent
    
  ],
  imports: [
    BrowserModule,
    HttpClientModule,FormsModule,AppRoutingModule,AngularFireModule.initializeApp(environment.firebaseConfig)
    ,AngularFireDatabaseModule
  ],
  providers: [PostService,CanDeactivateGuard,TeacherService],
  bootstrap: [AppComponent]
})
export class AppModule { }



