import {NgModule} from '@angular/core';
import { Routes, RouterModule, CanDeactivate } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ContactComponent } from './contact/contact.component';
import { WorkComponent } from './work/work.component';
import { PeopleComponent } from './people/people.component';
import { StudentsComponent } from './students/students.component';
import { FacultyComponent } from './faculty/faculty.component';
import { ResearchComponent } from './research/research.component';
import { NewPostComponent } from './home/posts/new-post/new-post.component';
import { CanDeactivateGuard } from './home/posts/new-post/can-deactivate-guard.service';
import { TeacherListEditComponent } from './faculty/teacher-list-edit/teacher-list-edit.component';


const appRoutes:Routes=[{path:'', component:HomeComponent },

{path:'research', component:ResearchComponent },
{path:'home', component:HomeComponent },
{path:'home/newPost', component:NewPostComponent ,canDeactivate:[CanDeactivateGuard]},
{path:'faculty', component:FacultyComponent },{path:'teacher-edit', component:TeacherListEditComponent },
{path:'students', component:StudentsComponent },
{path:'people', component:PeopleComponent },
{path:'work', component:WorkComponent },
{path:'contact', component:ContactComponent }]



@NgModule({

    imports:[RouterModule.forRoot(appRoutes)],
    exports:[RouterModule]
})
export class AppRoutingModule{}


