import { Component, OnInit } from '@angular/core';
import { TeacherService } from '../shared/teacher.service';
import {NgForm} from '@angular/forms';
@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.css']
})
export class TeacherComponent implements OnInit {
inserted=false;
updated=false;
deleted=false;
  constructor(private teacherService:TeacherService) { }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.teacherService.insertTeacher(form.value);
  this.resetForm(); this.inserted=true;}
  else {
     this.teacherService.updateTeacher(form.value);
    this.updated=true; }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.teacherService.selectedTeacher={
  $key:null,
  name:'',
  position:'',
  department:'',
  image:''
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.teacherService.deleteTeacher(form.value.$key);
    this.resetForm(form);
  }

}
}



